.blueStrip {
  background-color:#67c2db;
  z-index: 1;
  font-family: Oxygen, sans-serif;
  font-size: 18pt;
  color: white;
  align-self: stretch;
  position: relative;
  
  margin: 0;
  padding: 0;
  height: 60px;
  border-bottom: 15px solid rgb(233, 213, 35);
  
}

.blueStripText{
  margin: 0px;
  padding-left: 10px;
  position: absolute;
  bottom: -10%;
}